import { h } from "@vue/composition-api";
import { useConfig } from "@ss/mtd-vue2/es/config-provider";
import { computed, defineComponent, ref } from "@ss/mtd-adapter-vue2";

function getArcLength(percent, radius) {
  return percent * 2 * radius * Math.PI;
}

export default defineComponent({
  name: "MtdLoadingCircle",
  inheritAttrs: true,
  props: {
    thickness: {
      type: Number,
      default: 2
    },
    size: {
      type: Number,
      default: 20
    },
    color: {
      type: String,
      default: "currentColor"
    },
    disableShrink: {
      type: Boolean,
      default: false
    }
  },
  setup: function setup() {
    var config = useConfig();
    var prefixMTD = computed(function () {
      return config.getPrefix();
    });
    var scalePathTimer = ref(undefined);
    return {
      scalePathTimer: scalePathTimer,
      prefixMTD: prefixMTD
    };
  },
  computed: {
    sizeNumber: function sizeNumber() {
      return this.size;
    },
    diameter: function diameter() {
      return getArcLength(1, (this.sizeNumber - this.thickness) / 2);
    },
    radius: function radius() {
      return (this.sizeNumber - this.thickness) / 2;
    },
    center: function center() {
      return this.sizeNumber / 2;
    },
    style: function style() {
      return {
        width: "".concat(this.sizeNumber, "px"),
        height: "".concat(this.sizeNumber, "px")
      };
    }
  },
  watch: {
    disableShrink: function disableShrink(n) {
      !n ? this.scalePath(this.$refs.path) : clearTimeout(this.scalePathTimer);
    }
  },
  mounted: function mounted() {
    var path = this.$refs.path,
        disableShrink = this.disableShrink;

    if (!disableShrink) {
      this.scalePath(path);
    }
  },
  beforeUnmount: function beforeUnmount() {
    clearTimeout(this.scalePathTimer);
  },
  methods: {
    scalePath: function scalePath(path) {
      var _this = this;

      var step = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : 0;
      var diameter = this.diameter;
      step = step % 3;

      switch (step) {
        case 0:
          path.style.transitionDuration = "0ms";
          path.style.strokeDasharray = "3 ".concat(diameter);
          path.style.strokeDashoffset = "0";
          break;

        case 1:
          path.style.transitionDuration = "700ms";
          path.style.strokeDasharray = "".concat(0.7 * diameter, " ").concat(diameter);
          path.style.strokeDashoffset = -0.3 * diameter;
          break;

        case 2:
          path.style.transitionDuration = "700ms"; // Â§öÂá∫100msÁî®‰∫éÊòæÁ§∫ÊúÄÂêé‰∏ÄÁÇπ

          path.style.strokeDasharray = "".concat(0.7 * diameter, " ").concat(diameter);
          path.style.strokeDashoffset = 3 - diameter; // return

          break;
      }

      this.scalePathTimer = setTimeout(function () {
        _this.scalePath(path, step + 1);
      }, step ? 700 : 100);
    }
  },
  render: function render() {
    var style = this.style,
        sizeNumber = this.sizeNumber,
        center = this.center,
        radius = this.radius,
        thickness = this.thickness,
        color = this.color,
        prefixMTD = this.prefixMTD;
    return h("svg", {
      "style": style,
      "class": "".concat(prefixMTD, "-loading-circle"),
      "attrs": {
        "viewBox": "0 0 ".concat(sizeNumber, " ").concat(sizeNumber)
      }
    }, [h("circle", {
      "class": "",
      "ref": "path",
      "attrs": {
        "cx": center,
        "cy": center,
        "r": radius,
        "fill": "none",
        "stroke-linecap": "round",
        "stroke-width": thickness,
        "stroke": color
      },
      "style": "stroke-dasharray: 25px 1000px; stroke-dashoffset: 0px;"
    })]);
  }
});